import { reactive, ref, toRefs } from "vue";
import router from "@/router";
import { useRoute } from "vue-router";
export default {
  //
  emits: ["addaddress"],
  setup(props, {
    emit
  }) {
    //
    let route = useRoute();
    let num = ref(route.query.num);
    //
    let data = reactive({
      name: "",
      phone: "",
      large: "",
      datail: ""
    });
    // 
    const show = ref(false);
    const fieldValue = ref("");
    const cascaderValue = ref("");
    // children 
    const options = [{
      text: "",
      value: "330000",
      children: [{
        text: "",
        value: "330100",
        children: [{
          text: "",
          value: "330100"
        }, {
          text: "",
          value: "330101"
        }]
      }, {
        text: "",
        value: "330101",
        children: [{
          text: "",
          value: "330100"
        }, {
          text: "",
          value: "330101"
        }]
      }]
    }, {
      text: "",
      value: "320000",
      children: [{
        text: "",
        value: "320100",
        children: [{
          text: "",
          value: "330100"
        }, {
          text: "",
          value: "330101"
        }]
      }, {
        text: "",
        value: "320101",
        children: [{
          text: "",
          value: "330100"
        }]
      }]
    }];
    //  finish 
    const onFinish = ({
      selectedOptions
    }) => {
      show.value = false;
      fieldValue.value = selectedOptions.map(option => option.text).join("/");
    };
    // 
    const checked = ref(false);

    //
    let addlocal = () => {
      //
      if (route.query.id) {
        //
        let change = JSON.parse(window.localStorage.getItem("address"));
        //
        let index = change.findIndex(item => {
          return item.id == route.query.id;
        });
        //,
        change.splice(index, 1, {
          id: parseInt(route.query.id),
          name: data.name,
          phone: data.phone,
          large: fieldValue.value,
          datail: data.datail,
          checked: checked.value
        });
        window.localStorage.setItem("address", JSON.stringify(change));
        router.go(-1);
        //
      } else {
        //
        emit("addaddress", {
          id: 10,
          name: data.name,
          phone: data.phone,
          large: fieldValue.value,
          datail: data.datail,
          checked: checked.value
        });
        router.go(-1);
      }
    };
    // id
    if (route.query.id) {
      //
      let change = JSON.parse(window.localStorage.getItem("address"));
      //
      let index = change.findIndex(item => {
        return item.id == route.query.id;
      });
      data.name = change[index].name;
      data.phone = change[index].phone;
      data.datail = change[index].datail;
      fieldValue.value = change[index].large;
      checked.value = change[index].checked;
    }

    // 
    let del = () => {
      console.log("1");
      //
      let change = JSON.parse(window.localStorage.getItem("address"));
      //
      let index = change.findIndex(item => {
        return item.id == route.query.id;
      });
      //
      change.splice(index, 1);
      //
      window.localStorage.setItem("address", JSON.stringify(change));
      router.go(-1);
    };
    return {
      ...toRefs(data),
      // 
      show,
      options,
      onFinish,
      fieldValue,
      cascaderValue,
      //
      checked,
      //
      addlocal,
      //
      num,
      //
      del
    };
  }
};